<script setup lang="ts">
import useAsync from "~/composables/use-async";
import ClubsAPI from "~/services/club";
import useUserStore from "~/stores/user";
import getErrorLocalePathMessage from "~/utils/locale";

const { t, te } = useI18n();
const toaster = useToast();
const userStore = useUserStore();
const favoriteClubId = ref(userStore.user?.favoriteClubId || "");

const onSuccess = () => {
  toaster.success(t("pages.club.favorite_club_creating_successfully_text"));
  navigateTo("/");
};

const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};

const { loading: loadingSaveClub, run: onSaveClub } = useAsync(ClubsAPI.save, { onSuccess, onError });

function onSubmit() {
  onSaveClub(favoriteClubId.value);
}

const isContinueBtnDisabled = computed(() => loadingSaveClub.value || !favoriteClubId.value);

useHead({ title: t("labels.my_club") });
definePageMeta({ middleware: "auth" });
</script>

<template>
  <AuthHeader absolute transparent with-locale />
  <BaseMain>
    <div
      class="flex flex-col items-center justify-between h-full px-4 pt-32 pb-12 md:pt-48"
    >
      <div class="w-full max-w-[400px]">
        <h1 class="text-center text-heading-1-bold">
          {{ t("pages.clubs.choose_your_club") }}
        </h1>
        <p class="mt-4 text-xs text-center text-secondary">
          {{ t("pages.clubs.choose_your_club_message") }}
        </p>
        <form class="mt-8" @submit.prevent="onSubmit()">
          <BaseClubSelector
            v-model="favoriteClubId"
            :disabled="loadingSaveClub"
          />
          <BaseButton class="mt-10" name="continue-signup" full-width :disabled="isContinueBtnDisabled">
            {{ t("labels.continue") }}
          </BaseButton>
        </form>
      </div>
    </div>
  </BaseMain>
</template>
